  padding: 1rem;
  margin-bottom: 1.5rem;
}

.mood-label {
  font-size: 0.875rem;
  color: #6b7280;
  margin-bottom: 0.75rem;
}

.mood-display {
  display: flex;
  justify-content: center;
}

.mood-emoji {
