b,mBAAmB;EACnB,uBAAuB;EACvB,kBAAkB;EAClB,yCAAyC;AAC3C;;AAEA;EACE,qDAAqD;AACvD;;AAEA;EACE,qDAAqD;AACvD;;AAEA;EACE,qDAAqD;AACvD","sourcesContent":["* {\r\n    margin: 0;\r\n    padding: 0;\r\n    box-sizing: border-box;\r\n}\r\n\r\nbody, html {\r\n    height: 100%;\r\n    width: 100%;\r\n    font-family: Arial, sans-serif;\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    background-color: #f4f4f4;\r\n    margin: 0;\r\n}\r\n\r\n.container {\r\n    display: flex;\r\n    width: 100%;\r\n    height: 100%;\r\n    max-width: 100%;\r\n    background-color: #fff;\r\n    border-radius: 15px;\r\n    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);\r\n}\r\n\r\n.left-side, .right-side {\r\n    height: 100%;\r\n    width: 50%;  /* Ensure both sections take up equal space */\r\n    padding: 40px;\r\n}\r\n\r\n.left-side {\r\n    background-color: #EEF1DA;\r\n    text-align: center;\r\n    border-top-left-radius: 15px;\r\n    border-bottom-left-radius: 15px;\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: center;\r\n    align-items: center;\r\n}\r\n\r\n.left-side h1 {\r\n    font-size: 36px;\r\n    margin-bottom: 20px;\r\n}\r\n\r\n.left-side p {\r\n    font-size: 16px;\r\n    color: #333;\r\n    margin-bottom: 20px;\r\n}\r\n\r\n.left-side .btn-sign-in {\r\n    padding: 10px 20px;\r\n    background-color: #C7D9DD;\r\n    color: white;\r\n    border: none;\r\n    border-radius: 5px;\r\n    cursor: pointer;\r\n    font-size: 16px;\r\n    text-transform: uppercase;\r\n}\r\n\r\n.left-side .btn-sign-in:hover {\r\n    background-color: #45a049;\r\n}\r\n\r\n.right-side {\r\n    background-color: #fff;\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: center;\r\n    align-items: center;\r\n}\r\n\r\n.right-side h1 {\r\n    font-size: 30px;\r\n    margin-bottom: 20px;\r\n}\r\n\r\n.right-side p {\r\n    font-size: 16px;\r\n    color: #333;\r\n    margin-bottom: 20px;\r\n}\r\n\r\n/* Menambahkan container untuk input dan ikon */\r\n.input-with-icon {\r\n    display: flex;\r\n    align-items: center;\r\n    border: 1px solid #ddd;\r\n    border-radius: 8px;\r\n    padding: 10px;\r\n    background-color: transparent;\r\n    position: relative;\r\n    margin-bottom: 20px;\r\n}\r\n\r\n.input-with-icon input {\r\n    width: 600px;\r\n    padding: 15px;\r\n    font-size: 16px;\r\n    border: none;\r\n    outline: none;\r\n    background-color: transparent;\r\n    margin-left: 10px\r\n}\r\n\r\n.input-with-icon input:focus {\r\n    border-color: #4CAF50;\r\n}\r\n\r\n/* Mengatur posisi dan ukuran ikon */\r\n.toggle-password {\r\n    position: absolute;\r\n    right: 35px; /* Memberi jarak dari kanan */\r\n    cursor: pointer;\r\n    width: 20px; /* Ukuran ikon */\r\n    height: 20px; /* Ukuran ikon */\r\n}\r\n\r\n\r\n.form-group input {\r\n    width: 500px; /* Mengatur panjang input lainnya */\r\n    padding: 15px;\r\n    font-size: 16px;\r\n    border: 1px solid #ddd;\r\n    border-radius: 8px;\r\n    outline: none;\r\n}\r\n\r\n.form-group input:focus {\r\n    border-color: #4CAF50;\r\n}\r\n\r\n.btn-sign-in {\r\n    width: 100%;\r\n    padding: 15px;\r\n    background-color: #C7D9DD;\r\n    color: white;\r\n    border: none;\r\n    border-radius: 8px;\r\n    font-size: 18px;\r\n    cursor: pointer;\r\n    text-transform: uppercase;\r\n}\r\n\r\n.btn-sign-up:hover {\r\n    background-color: #45a049;\r\n}\r\n\r\n/*login*/\r\n/* Styling untuk halaman login */\r\n.login-container {\r\n    display: flex;\r\n    width: 100%;\r\n    height: 100%;\r\n    max-width: 100%;\r\n    background-color: #fff;\r\n    border-radius: 15px;\r\n    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);\r\n}\r\n\r\n.login-left-side, .login-right-side {\r\n    height: 100%;\r\n    width: 50%;\r\n    padding: 40px;\r\n}\r\n\r\n.login-left-side {\r\n    background-color: #ffffff;\r\n    text-align: center;\r\n    border-top-left-radius: 15px;\r\n    border-bottom-left-radius: 15px;\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: center;\r\n    align-items: center;\r\n}\r\n\r\n.login-left-side h1 {\r\n    font-size: 36px;\r\n    margin-bottom: 20px;\r\n}\r\n\r\n.login-left-side p {\r\n    font-size: 16px;\r\n    color: #333;\r\n    margin-bottom: 20px;\r\n}\r\n\r\n.login-left-side .btn-sign-in {\r\n    padding: 10px 20px;\r\n    background-color: #C7D9DD;\r\n    color: white;\r\n    border: none;\r\n    border-radius: 5px;\r\n    cursor: pointer;\r\n    font-size: 16px;\r\n    text-transform: uppercase;\r\n}\r\n\r\n.login-left-side .btn-sign-in:hover {\r\n    background-color: #45a049;\r\n}\r\n\r\n.login-right-side {\r\n    background-color: #EEF1DA;\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: center;\r\n    align-items: center;\r\n}\r\n\r\n.login-right-side h1 {\r\n    font-size: 30px;\r\n    margin-bottom: 20px;\r\n}\r\n\r\n.login-right-side p {\r\n    font-size: 16px;\r\n    color: #333;\r\n    margin-bottom: 20px;\r\n}\r\n\r\n.login-form-group input {\r\n    width: 300px;\r\n    padding: 15px;\r\n    font-size: 16px;\r\n    border: 1px solid #ddd;\r\n    border-radius: 8px;\r\n    outline: none;\r\n    margin-bottom: 10px;\r\n}\r\n\r\n.login-form-group input:focus {\r\n    border-color: #4CAF50;\r\n}\r\n\r\n.login-toggle-password {\r\n    position: absolute;\r\n    right: 20px;\r\n    top: 50%;\r\n    transform: translateY(-50%);\r\n    cursor: pointer;\r\n}\r\n\r\n.forgot-password {\r\n    font-size: 14px;\r\n    color: #333;\r\n    text-decoration: none;\r\n    margin-top: 10px;\r\n}\r\n\r\n.forgot-password:hover {\r\n    text-decoration: underline;\r\n}\r\n\r\n.login-btn-sign-in {\r\n    width: 100%;\r\n    padding: 15px;\r\n    background-color: #C7D9DD;\r\n    color: white;\r\n    border: none;\r\n    border-radius: 8px;\r\n    font-size: 18px;\r\n    cursor: pointer;\r\n    text-transform: uppercase;\r\n}\r\n\r\n.login-btn-sign-in:hover {\r\n    background-color: #45a049;\r\n}\r\n\r\n.login-btn-sign-up {\r\n    width: 100%;\r\n    padding: 15px;\r\n    background-color: #C7D9DD;\r\n    color: white;\r\n    border: none;\r\n    border-radius: 8px;\r\n    font-size: 18px;\r\n    cursor: pointer;\r\n    text-transform: uppercase;\r\n}\r\n\r\n.login-btn-sign-up:hover {\r\n    background-color: #45a049;\r\n}\r\n\r\n/* Modal Forgot Password */\r\n* {\r\n  margin: 0;\r\n  padding: 0;\r\n  box-sizing: border-box;\r\n}\r\n\r\nhtml, body {\r\n  height: 100%;\r\n  background: #f7f7f7 !important; /* Override background image dari styles.css */\r\n  font-family: Arial, sans-serif !important;\r\n  color: #000 !important;\r\n  overflow-x: hidden;\r\n}\r\n\r\n\r\n.forgot-password-container {\r\n  background-color: #fff !important;\r\n  padding: 60px 50px !important;   /* Padding diperbesar */\r\n  border-radius: 20px !important;  /* Radius agak lebih besar */\r\n  max-width: 600px !important;     /* Lebar container diperbesar */\r\n  width: 100% !important;\r\n  margin: 80px auto !important;    /* Margin atas bawah lebih besar */\r\n  text-align: center !important;\r\n  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2) !important; /* Shadow lebih tebal */\r\n}\r\n\r\n.forgot-password-container h1 {\r\n  margin-bottom: 25px;\r\n  font-weight: 900;\r\n  font-size: 28px;\r\n  color: #333;\r\n}\r\n\r\n.forgot-password-container p {\r\n  font-size: 16px;\r\n  color: #666;\r\n  margin-bottom: 30px;\r\n}\r\n\r\n.forgot-password-container input[type=\"email\"] {\r\n  width: 100%;\r\n  padding: 12px 15px;\r\n  font-size: 16px;\r\n  border: 1px solid #ccc;\r\n  border-radius: 8px;\r\n  margin-bottom: 30px;\r\n  box-sizing: border-box;\r\n  transition: border-color 0.3s ease;\r\n}\r\n\r\n.forgot-password-container input[type=\"email\"]:focus {\r\n  outline: none;\r\n  border-color: #75c4d6;\r\n}\r\n\r\n.forgot-password-container button {\r\n  width: 100%;\r\n  padding: 12px 0;\r\n  background-color: #75c4d6;\r\n  border: none;\r\n  border-radius: 8px;\r\n  color: white;\r\n  font-size: 18px;\r\n  font-weight: 700;\r\n  cursor: pointer;\r\n  transition: background-color 0.3s ease;\r\n}\r\n\r\n.forgot-password-container button:hover {\r\n  background-color: #5ea0b8;\r\n}\r\n\r\n.back-to-login {\r\n  margin-top: 20px;\r\n  display: inline-block;\r\n  color: #75c4d6;\r\n  text-decoration: underline;\r\n  cursor: pointer;\r\n  font-size: 14px;\r\n}\r\n\r\n\r\n\r\n/* styling homepage */\r\n/* Reset styling */\r\n* {\r\n    margin: 0;\r\n    padding: 0;\r\n    box-sizing: border-box;\r\n}\r\n\r\nbody {\r\n    font-family: Arial, sans-serif;\r\n}\r\n\r\n/* Navbar Styling */\r\n.navbar {\r\n    display: flex;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n    padding: 15px 50px;\r\n    background-color: #fff;\r\n    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);\r\n    position: fixed;\r\n    width: 100%;\r\n    top: 0;\r\n    left: 0;\r\n    z-index: 1000;\r\n}\r\n\r\n.navbar .logo {\r\n    font-size: 16px;\r\n    font-weight: bold;\r\n    color: #333;\r\n}\r\n\r\n.navbar .logo img {\r\n    width: 180px;   /* Menentukan lebar gambar */\r\n    height: auto;   /* Menjaga rasio aspek gambar tetap */\r\n}\r\n\r\nnav.nav ul {\r\n  list-style: none;\r\n  display: flex;\r\n  gap: 25px;\r\n  padding: 0;\r\n  margin: 0;\r\n}\r\n\r\nnav.nav ul li a {\r\n  text-decoration: none;\r\n  color: black;\r\n  font-weight: 600;\r\n}\r\n\r\nnav.nav ul li a.active, nav.nav ul li a:hover {\r\n  border-bottom: 2px solid black;\r\n  padding-bottom: 4px;\r\n}\r\n\r\n/* Main Content */\r\n.main-content-home {\r\n    padding: 450px 40px 40px 40px; /* Adding padding to the top for space below navbar */\r\n    background-color: #f9f9f9;\r\n}\r\n\r\n.greeting {\r\n    background-color: #EEF1DA;\r\n    padding: 40px;\r\n    border-radius: 20px;\r\n    margin-top: 900px;\r\n    display: flex;  /* Using flexbox to arrange greeting content and placeholder side by side */\r\n    justify-content: space-between;\r\n}\r\n\r\n.greeting-content {\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: space-between;\r\n    width: 100%;\r\n}\r\n\r\n.greeting-text {\r\n    max-width: 45%; /* Limiting width of text area */\r\n    text-align: left;\r\n}\r\n\r\n.greeting h1 {\r\n    font-size: 36px;\r\n    margin-bottom: 10px;\r\n}\r\n\r\n.greeting p {\r\n    font-size: 18px;\r\n    color: #333;\r\n    margin-bottom: 20px;\r\n}\r\n\r\n.start-journaling-btn {\r\n    padding: 15px 30px;\r\n    background-color: #75c4d6;\r\n    color: white;\r\n    border: none;\r\n    border-radius: 8px;\r\n    font-size: 18px;\r\n    cursor: pointer;\r\n}\r\n\r\n.start-journaling-btn:hover {\r\n    background-color: #45a049;\r\n}\r\n\r\n/* Styling for placeholder */\r\n.placeholder {\r\n    width: 650px;\r\n    height: 350px;\r\n    background-color: #e0e0e0;\r\n    border-radius: 8px;\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    color: #333;\r\n    font-size: 18px;\r\n    margin-left: 20px;\r\n}\r\n\r\n/* Day Streak Section in a Box */\r\n.day-streak-box {\r\n    margin-top: 50px;\r\n    background-color: #e0f2f1;\r\n    padding: 20px;\r\n    border-radius: 15px;\r\n}\r\n\r\n/* Container for day streak and streak details */\r\n.day-streak-content {\r\n    display: flex;\r\n    justify-content: space-between;\r\n}\r\n\r\n.streak-left {\r\n    width: 60%; /* Left side of the box */\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: center;\r\n}\r\n\r\n.streak-header {\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n    margin-bottom: 20px;\r\n}\r\n\r\n.streak-icon {\r\n    font-size: 30px;\r\n    margin-right: 10px;\r\n}\r\n\r\n.day-streak h2 {\r\n    font-size: 28px;\r\n}\r\n\r\n.streak {\r\n    display: flex;\r\n    justify-content: center;\r\n    margin-bottom: 20px;\r\n}\r\n\r\n.streak .day {\r\n    width: 40px;\r\n    height: 40px;\r\n    border-radius: 50%;\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n    margin: 0 5px;\r\n    background-color: #e0e0e0;\r\n    color: white;\r\n    font-size: 16px;\r\n}\r\n\r\n.streak .day.active {\r\n    background-color: #4CAF50;\r\n}\r\n\r\n/* Right side for current streak and total diary */\r\n.streak-right {\r\n    width: 35%; /* Right side of the box */\r\n    text-align: left;\r\n    padding-left: 20px;\r\n}\r\n\r\n.streak-details {\r\n    font-size: 20px;\r\n    color: #333;\r\n    margin-top: 50px;\r\n}\r\n\r\n.streak-details p {\r\n    margin: 5px 0;\r\n}\r\n\r\n.streak-message {\r\n    font-size: 16px;\r\n    color: #4CAF50;\r\n    margin-top: 20px;\r\n    font-weight: bold;\r\n}\r\n\r\n/* Latest Diary Section */\r\n.latest-diary {\r\n    margin-top: 20px;\r\n    background-color: #EEF1DA;\r\n    padding: 20px;\r\n    border-radius: 15px;\r\n}\r\n\r\n/* Diary Box */\r\n.diary-box {\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: space-between;\r\n    background-color: #EEF1DA;\r\n    border-radius: 10px;\r\n}\r\n\r\n/* Left side: Mood Icon or Image */\r\n.diary-left {\r\n    flex: 1;\r\n    display: flex;\r\n    justify-content: center;\r\n}\r\n\r\n.diary-image {\r\n    width: 180px;\r\n    border-radius: 20%;\r\n    object-fit: cover;\r\n}\r\n\r\n/* Center: Diary Text */\r\n.diary-center {\r\n    flex: 3;\r\n    padding: 0 20px;\r\n}\r\n\r\n.diary-text {\r\n    font-size: 16px;\r\n    color: #333;\r\n    line-height: 1.5;\r\n    font-weight: normal;\r\n}\r\n\r\n/* Right side: Date */\r\n/* Right side: Emoji and Date */\r\n.diary-right {\r\n    flex: 1;\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: center;\r\n}\r\n\r\n/* Emoji Container */\r\n.emoji-container {\r\n    margin-bottom: 10px;\r\n}\r\n\r\n.emoji {\r\n    width: 80px;\r\n    border-radius: 50%;\r\n    object-fit: cover;\r\n}\r\n\r\n/* Date Styling */\r\n.diary-date {\r\n    font-size: 14px;\r\n    color: #333;\r\n    font-weight: bold;\r\n}\r\n\r\n.recap-week-section {\r\n  max-width: 2000px;\r\n  margin: 50px auto 0;\r\n  padding: 0 20px;\r\n  font-family: Arial, sans-serif;\r\n}\r\n\r\n.recap-week-header {\r\n  color: #2f2e56;\r\n  font-weight: 700;\r\n  font-size: 24px;\r\n  margin-bottom: 15px;\r\n}\r\n\r\n.recap-week-navigation {\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  gap: 20px;\r\n  margin-bottom: 20px;\r\n}\r\n\r\n.nav-arrow {\r\n  font-size: 24px;\r\n  background: none;\r\n  border: none;\r\n  cursor: pointer;\r\n  color: #2f2e56;\r\n  padding: 5px 15px;\r\n  border-radius: 10px;\r\n  transition: background-color 0.3s ease;\r\n}\r\n\r\n.nav-arrow:hover {\r\n  background-color: #c9d3d9;\r\n}\r\n\r\n.date-range {\r\n  background-color: #c9d3d9;\r\n  padding: 10px 25px;\r\n  border-radius: 30px;\r\n  font-weight: 700;\r\n  color: #000;\r\n  font-size: 16px;\r\n  min-width: 200px;\r\n  text-align: center;\r\n}\r\n\r\n.recap-week-list {\r\n  display: flex;\r\n  overflow-x: auto;\r\n  gap: 20px;\r\n  padding-bottom: 10px;\r\n}\r\n\r\n.recap-item {\r\n  background-color: #c9d3d9;\r\n  border-radius: 30px;\r\n  display: flex;\r\n  padding: 20px;\r\n  min-width: 320px;\r\n  flex-shrink: 0;\r\n  box-shadow: 0 4px 8px rgba(0,0,0,0.1);\r\n}\r\n\r\n.emoji-container {\r\n  flex-shrink: 0;\r\n  background-color: #eaefdf;\r\n  border-radius: 12px;\r\n  width: 70px;\r\n  height: 70px;\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n  margin-right: 20px;\r\n}\r\n\r\n.emoji-container img {\r\n  width: 40px;\r\n  height: 40px;\r\n}\r\n\r\n.recap-text {\r\n  flex-grow: 1;\r\n  display: flex;\r\n  flex-direction: column;\r\n  justify-content: center;\r\n}\r\n\r\n.recap-date {\r\n  font-weight: 700;\r\n  font-size: 16px;\r\n  color: #1a1a1a;\r\n  margin-bottom: 8px;\r\n}\r\n\r\n.recap-desc {\r\n  font-size: 14px;\r\n  color: #1a1a1a;\r\n  line-height: 1.4;\r\n}\r\n\r\n.start-story-section {\r\n  max-width: 900px;\r\n  margin: 50px auto;\r\n  padding: 0 20px;\r\n  font-family: Arial, sans-serif;\r\n}\r\n\r\n.start-story-header {\r\n  text-align: center;\r\n  font-weight: 900;\r\n  font-size: 24px;\r\n  margin-bottom: 30px;\r\n  color: #000;\r\n}\r\n\r\n.start-story-container {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  gap: 20px;\r\n}\r\n\r\n.story-box {\r\n  background-color: #f3f8d4;\r\n  border-radius: 15px;\r\n  padding: 40px;       \r\n  flex: 2;           \r\n  box-sizing: border-box;\r\n  min-width: 250px;    \r\n}\r\n\r\n.story-box ul {\r\n  list-style: disc inside;\r\n  font-weight: bold;\r\n  font-size: 15px;  \r\n  color: #000;\r\n  line-height: 1.4;\r\n  padding-left: 0;\r\n}\r\n\r\n.story-box ul li {\r\n  margin-bottom: 10px;\r\n  margin-bottom: 15px;\r\n}\r\n\r\n/*footer styling*/\r\n.footer {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n  background-color: #EEF1DA; \r\n  width: 100%;\r\n  color: #000;\r\n  font-family: Arial, sans-serif;\r\n  font-size: 14px;\r\n}\r\n\r\n.footer-left {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 15px;\r\n}\r\n\r\n.footer-logo {\r\n  width: 200px;   /* atur ukuran logo */\r\n  height: auto;\r\n}\r\n\r\n.footer-title {\r\n  font-weight: 700;\r\n  font-size: 18px;\r\n}\r\n\r\n.footer-center {\r\n  max-width: 400px;\r\n  text-align: left;\r\n}\r\n\r\n.footer-center h3,\r\n.footer-right h3 {\r\n  margin-bottom: 10px;\r\n  font-weight: 700;\r\n}\r\n\r\n.footer-center p {\r\n  font-size: 14px;\r\n  line-height: 1.4;\r\n  color: #222;\r\n}\r\n\r\n.footer-right {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 10px;\r\n  text-align: left;\r\n}\r\n\r\n.contact-item {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 10px;\r\n}\r\n\r\n.contact-icon {\r\n  width: 18px;\r\n  height: 18px;\r\n}\r\n\r\n.footer-right a {\r\n  color: #000;\r\n  text-decoration: none;\r\n  font-size: 14px;\r\n}\r\n\r\n.footer-right a:hover {\r\n  text-decoration: underline;\r\n}\r\n\r\n/* Styling untuk footer */\r\n.footer-global {\r\n  background-color: #fff;  /* Warna latar belakang footer putih */\r\n  color: #000;             /* Warna teks hitam */\r\n  text-align: center;      /* Menyelaraskan teks ke tengah */\r\n  padding: 20px 0;         /* Padding atas dan bawah untuk memberi ruang */\r\n  font-size: 14px;         /* Ukuran font */\r\n  margin-top: 2px;        /* Memberi jarak antara footer dengan konten di atasnya */\r\n  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Menambahkan shadow pada footer */\r\n  width: 100%;             /* Memastikan footer memanjang sepanjang lebar layar */\r\n  position: relative;      /* Posisi relative untuk menjaga footer di bawah */\r\n}\r\n\r\n/* Membuat halaman lebih tinggi jika konten tidak cukup panjang */\r\nbody {\r\n  min-height: 100vh;       /* Memastikan body memenuhi seluruh tinggi viewport */\r\n  display: flex;\r\n  flex-direction: column;\r\n  margin: 0;\r\n}\r\n\r\n/* Memastikan konten utama berada di atas dan footer di bawah */\r\n.main-content {\r\n  flex-grow: 1;            /* Membuat konten utama mengisi sisa ruang */\r\n}\r\n\r\n\r\n\r\n/* Journaling Page */\r\n/* Reset styling */\r\n* {\r\n    margin: 0;\r\n    padding: 0;\r\n    box-sizing: border-box;\r\n}\r\n\r\nbody {\r\n    font-family: Arial, sans-serif;\r\n}\r\n\r\n.judul-area {\r\n  font-size: 1.5rem; /* mirip h2 */\r\n  font-weight: bold;\r\n  padding: 1rem;\r\n  width: 90%;\r\n  background-color: transparent;\r\n  border: 1px solid transparent;\r\n  border-radius: 8px;\r\n  resize: none;\r\n  text-align: center;\r\n}\r\n\r\n.judul-area::placeholder {\r\n  color: #999;\r\n}\r\n\r\n\r\nbody#journaling {\r\n  background-image: url('../assets/background.png') !important;\r\n  background-size: cover !important;\r\n  background-position: center center !important;\r\n  background-attachment: fixed !important;\r\n}\r\n\r\n.journaling-header {\r\n  text-align: center;\r\n  margin-bottom: 30px;\r\n}\r\n\r\n.journaling-header h2 {\r\n  font-size: 28px;\r\n  font-weight: 700;\r\n  margin-bottom: 10px;\r\n}\r\n\r\n.journaling-date {\r\n  font-size: 16px;\r\n  color: #888;\r\n  margin-bottom: 10px;\r\n}\r\n\r\n.journaling-description {\r\n  font-size: 16px;\r\n  color: #333;\r\n}\r\n\r\n.journaling-body {\r\n  margin-bottom: 20px;\r\n}\r\n\r\n.journaling-textarea {\r\n  width: 100%;\r\n  height: 250px;\r\n  padding: 15px;\r\n  font-size: 16px;\r\n  border: 1px solid #ddd;\r\n  border-radius: 8px;\r\n  outline: none;\r\n  resize: none;\r\n  background-color: transparent;\r\n}\r\n\r\n.journaling-textarea:focus {\r\n  border-color: #4CAF50;\r\n}\r\n\r\n.journaling-footer {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n}\r\n\r\n.save-btn {\r\n  padding: 15px 30px;\r\n  background-color: #75c4d6;\r\n  color: white;\r\n  border: none;\r\n  border-radius: 8px;\r\n  font-size: 18px;\r\n  cursor: pointer;\r\n}\r\n\r\n.save-btn:hover {\r\n  background-color: #45a049;\r\n}\r\n\r\n.icon-group {\r\n  display: flex;\r\n  gap: 10px;\r\n}\r\n\r\n.icon {\r\n  width: 30px;\r\n  height: 30px;\r\n  cursor: pointer;\r\n}\r\n\r\n.journaling-container {\r\n  width: 100%;\r\n  max-width: 1000px;\r\n  margin: 80px auto;  /* Margin atas dan bawah */\r\n  margin-top: 200px;  /* Jarak antara navbar dan journaling container */\r\n  padding-bottom: 550px;\r\n  background-color: #fff9f9;\r\n  border-radius: 15px;\r\n  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);\r\n  padding: 2rem 1.5rem;\r\n  gap: 20px;\r\n}\r\n\r\n\r\n/* Tambahan kalender */\r\n.calendar-title {\r\n  text-align: center;\r\n  font-size: 18px;\r\n  font-weight: bold;\r\n  margin-bottom: 10px;\r\n}\r\n\r\n.calendar-days, .calendar-dates {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  gap: 10px;\r\n  text-align: center;\r\n  font-weight: 500;\r\n}\r\n\r\n.calendar-days div, .calendar-dates div {\r\n  width: 30px;\r\n}\r\n\r\n/* Diary Box Custom */\r\n.diary-box-custom {\r\n  background-color: #e2e2df;\r\n  padding: 30px;\r\n  border-radius: 10px;\r\n  box-shadow: 0 2px 5px rgba(0,0,0,0.1);\r\n  max-width: 700px;\r\n  margin: 0 auto;\r\n}\r\n\r\n.diary-date-full {\r\n  font-size: 18px;\r\n  font-weight: bold;\r\n  margin-bottom: 20px;\r\n}\r\n\r\n.feeling-box {\r\n  background-color: #b8b8b8;\r\n  border-radius: 10px;\r\n  padding: 15px;\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 15px;\r\n  color: #000;\r\n  font-weight: 500;\r\n  width: fit-content;\r\n}\r\n\r\n.emoji-circle {\r\n  width: 50px;\r\n  height: 50px;\r\n  border-radius: 50%;\r\n  background-color: #c4e19a;\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n  overflow: hidden;\r\n}\r\n\r\n.emoji-circle img {\r\n  width: 30px;\r\n  height: 30px;\r\n}\r\n\r\n.diary-placeholder {\r\n  margin-top: 20px;\r\n  font-style: italic;\r\n  color: #333;\r\n}\r\n\r\n/* General Styles History*/\r\n\r\n.main-content-history {\r\n  max-width: 800px;\r\n  margin: 0 auto;\r\n  padding: 2rem 1.5rem;\r\n  padding-top: 800px; /* Space for fixed navbar */\r\n}\r\n\r\n.card {\r\n  background: rgba(255, 255, 255, 0.8);\r\n  backdrop-filter: blur(10px);\r\n  border-radius: 16px;\r\n  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1);\r\n  padding: 1.5rem;\r\n  margin-bottom: 2rem;\r\n}\r\n\r\n/* Calendar Section */\r\n.calendar-header {\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: space-between;\r\n  margin-bottom: 1.5rem;\r\n}\r\n\r\n.calendar-title {\r\n  font-size: 1.125rem;\r\n  font-weight: 600;\r\n  color: #1f2937;\r\n}\r\n\r\n.calendar-nav {\r\n  display: flex;\r\n  gap: 0.5rem;\r\n}\r\n\r\n.nav-btn {\r\n  padding: 0.5rem;\r\n  border: none;\r\n  background: transparent;\r\n  border-radius: 8px;\r\n  cursor: pointer;\r\n  transition: background-color 0.2s;\r\n}\r\n\r\n.nav-btn:hover {\r\n  background: #f3f4f6;\r\n}\r\n\r\n.calendar-grid {\r\n  display: grid;\r\n  grid-template-columns: repeat(7, 1fr);\r\n  gap: 0.5rem;\r\n}\r\n\r\n.day-header {\r\n  text-align: center;\r\n  font-size: 0.875rem;\r\n  font-weight: 500;\r\n  color: #6b7280;\r\n  padding: 0.5rem 0;\r\n}\r\n\r\n.calendar-day {\r\n  aspect-ratio: 1;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  border-radius: 8px;\r\n  font-size: 0.875rem;\r\n  font-weight: 500;\r\n  cursor: pointer;\r\n  transition: all 0.2s;\r\n  border: none;\r\n  background: transparent;\r\n}\r\n\r\n.calendar-day.current-month {\r\n  color: #374151;\r\n}\r\n\r\n.calendar-day.other-month {\r\n  color: #d1d5db;\r\n}\r\n\r\n.calendar-day.selected {\r\n  background: #3b82f6;\r\n  color: white;\r\n  box-shadow: 0 4px 12px rgba(59, 130, 246, 0.4);\r\n}\r\n\r\n.calendar-day.current-month:hover:not(.selected) {\r\n  background: #f3f4f6;\r\n}\r\n\r\n/* Journal Section */\r\n.journal-header h3 {\r\n  font-size: 1.125rem;\r\n  font-weight: 600;\r\n  color: #1f2937;\r\n  margin-bottom: 1rem;\r\n}\r\n\r\n.mood-section {\r\n  background: #f3f4f6;\r\n  border-radius: 12px;\r\n  padding: 1rem;\r\n  margin-bottom: 1.5rem;\r\n}\r\n\r\n.mood-label {\r\n  font-size: 0.875rem;\r\n  color: #6b7280;\r\n  margin-bottom: 0.75rem;\r\n}\r\n\r\n.mood-display {\r\n  display: flex;\r\n  justify-content: center;\r\n}\r\n\r\n.mood-emoji {\r\n  width: 64px;\r\n  height: 64px;\r\n  background: #bbf7d0;\r\n  border-radius: 16px;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  font-size: 2rem;\r\n}\r\n\r\n.diary-section {\r\n  margin-top: 1rem;\r\n}\r\n\r\n.diary-header {\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: between;\r\n  margin-bottom: 1rem;\r\n}\r\n\r\n.diary-title {\r\n  font-weight: 500;\r\n  color: #374151;\r\n  flex-grow: 1;\r\n}\r\n\r\n.edit-btn {\r\n  padding: 0.5rem;\r\n  border: none;\r\n  background: transparent;\r\n  border-radius: 8px;\r\n  cursor: pointer;\r\n  transition: background-color 0.2s;\r\n  margin-left: auto;\r\n}\r\n\r\n.edit-btn:hover {\r\n  background: #f3f4f6;\r\n}\r\n\r\n.diary-content {\r\n  background: #f9fafb;\r\n  border-radius: 12px;\r\n  padding: 1rem;\r\n  min-height: 120px;\r\n}\r\n\r\n.diary-text {\r\n  color: #6b7280;\r\n  font-size: 0.875rem;\r\n  line-height: 1.6;\r\n}\r\n\r\n.action-buttons {\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: flex-end;\r\n  gap: 0.75rem;\r\n  margin-top: 1.5rem;\r\n}\r\n\r\n.btn-secondary {\r\n  padding: 0.5rem 1rem;\r\n  border: none;\r\n  background: transparent;\r\n  color: #6b7280;\r\n  cursor: pointer;\r\n  transition: color 0.2s;\r\n}\r\n\r\n.btn-secondary:hover {\r\n  color: #374151;\r\n}\r\n\r\n.btn-primary {\r\n  padding: 0.5rem 1.5rem;\r\n  background: #3b82f6;\r\n  color: white;\r\n  border: none;\r\n  border-radius: 8px;\r\n  cursor: pointer;\r\n  transition: background-color 0.2s;\r\n}\r\n\r\n.btn-primary:hover {\r\n  background: #2563eb;\r\n}\r\n\r\n/* Icons */\r\n.icon {\r\n  width: 20px;\r\n  height: 20px;\r\n  fill: currentColor;\r\n}\r\n\r\n.icon-small {\r\n  width: 16px;\r\n  height: 16px;\r\n  fill: currentColor;\r\n}\r\n\r\n/* Profile page styling */\r\n.main-content-profile {\r\n  max-width: 800px;\r\n  margin: 0 auto;\r\n  padding: 2rem 1.5rem;\r\n  padding-top: 250px; /* Space for fixed navbar */\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 20px; /* Memberi jarak antara navbar dan profile-container */\r\n}\r\n\r\n.profile-container {\r\n  background-color: #fff;\r\n  padding: 40px;\r\n  border-radius: 15px;\r\n  width: 900px;\r\n  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);\r\n  border: 1px solid #ddd;\r\n  margin-top: 20px;\r\n  padding-top: 40px;\r\n}\r\n\r\n.profile-header {\r\n  display: flex;\r\n  gap: 30px;\r\n}\r\n\r\n.profile-content {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  gap: 20px;\r\n  width: 100%;\r\n}\r\n\r\n.profile-picture {\r\n  flex: 1;\r\n  display: flex;\r\n  justify-content: center;\r\n  margin-top: 100px;\r\n}\r\n\r\n.profile-img {\r\n  width: 200px;\r\n  height: 200px;\r\n  border-radius: 50%;\r\n}\r\n\r\n.profile-info {\r\n  flex: 2;\r\n}\r\n\r\n.profile-info h1 {\r\n  font-size: 24px;\r\n  font-weight: bold;\r\n  margin-bottom: 20px;\r\n}\r\n\r\n.profile-row {\r\n  margin-bottom: 20px;\r\n}\r\n\r\n.profile-row label {\r\n  display: block;\r\n  font-size: 16px;\r\n  margin-bottom: 5px;\r\n}\r\n\r\n.profile-row input {\r\n  width: 100%;\r\n  padding: 12px;\r\n  font-size: 16px;\r\n  border: 1px solid #ddd;\r\n  border-radius: 8px;\r\n  background-color: #f4f4f4;\r\n  color: #333;\r\n  margin-bottom: 5px;\r\n}\r\n\r\n.change-btn {\r\n  font-size: 14px;\r\n  color: #4a90e2;\r\n  cursor: pointer;\r\n}\r\n\r\n.change-btn:hover {\r\n  text-decoration: underline;\r\n}\r\n\r\n.logout-btn {\r\n  text-align: center;\r\n  margin-top: 30px;\r\n}\r\n\r\n.logout-btn button {\r\n  padding: 10px 20px;\r\n  background-color: #d9534f;\r\n  color: white;\r\n  font-size: 16px;\r\n  border: none;\r\n  border-radius: 5px;\r\n  cursor: pointer;\r\n}\r\n\r\n.logout-btn button:hover {\r\n  background-color: #c9302c;\r\n}\r\n\r\n/* Styling untuk modal */\r\n.modal {\r\n  display: none;  /* Sembunyikan modal secara default */\r\n  position: fixed;\r\n  top: 0;\r\n  left: 0;\r\n  width: 100%;\r\n  height: 100%;\r\n  background-color: rgba(0, 0, 0, 0.5);\r\n  justify-content: center;\r\n  align-items: center;\r\n  z-index: 1000;\r\n}\r\n\r\n/* Styling untuk modal */\r\n.modal {\r\n  display: none;  /* Sembunyikan modal secara default */\r\n  position: fixed;\r\n  top: 0;\r\n  left: 0;\r\n  width: 100%;\r\n  height: 100%;\r\n  background-color: rgba(0, 0, 0, 0.5);\r\n  justify-content: center;\r\n  align-items: center;\r\n  z-index: 1000;\r\n}\r\n\r\n/* Gaya untuk konten modal */\r\n.modal-content {\r\n  background-color: white;\r\n  padding: 20px;\r\n  border-radius: 10px;\r\n  width: 600px;\r\n  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);\r\n}\r\n\r\n/* Tombol tutup */\r\n.close-btn {\r\n  position: absolute;\r\n  top: 10px;\r\n  right: 20px;\r\n  font-size: 30px;\r\n  font-weight: bold;\r\n  cursor: pointer;\r\n}\r\n\r\n/* Styling untuk input di modal */\r\n/* Styling untuk modal */\r\n.modal-row {\r\n  margin-bottom: 20px;\r\n  position: relative;\r\n}\r\n\r\n.modal-row label {\r\n  display: block;\r\n  margin-bottom: 10px;\r\n  font-size: 18px;\r\n  font-weight: bold;\r\n}\r\n\r\n.modal-row input {\r\n  width: 100%;\r\n  padding: 15px 20px;  /* Menambahkan padding lebih besar untuk memberi ruang pada ikon */\r\n  font-size: 16px;\r\n  border: 1px solid #ddd;\r\n  border-radius: 8px;\r\n  background-color: #f4f4f4;\r\n  position: relative; /* Menyediakan ruang untuk ikon */\r\n  padding-right: 50px; /* Memberikan ruang lebih besar di kanan untuk ikon */\r\n}\r\n\r\n.modal-row .toggle-password {\r\n  cursor: pointer;\r\n  position: absolute;\r\n  top: 65%;  /* Pastikan ikon berada di tengah */\r\n  right: 20px;  /* Menempatkan ikon lebih tepat di kanan */\r\n  transform: translateY(-50%); /* Menjaga posisi vertikal di tengah */\r\n}\r\n\r\n#eye-icon {\r\n  width: 25px;  /* Sesuaikan ukuran ikon */\r\n  height: 25px;  /* Sesuaikan ukuran ikon */\r\n}\r\n\r\n\r\n/* General Modal Styling */\r\n.modal {\r\n  display: none; /* Hidden by default */\r\n  position: fixed; /* Stay in place */\r\n  z-index: 1; /* Sit on top */\r\n  left: 0;\r\n  top: 0;\r\n  width: 100%; /* Full width */\r\n  height: 100%; /* Full height */\r\n  overflow: auto; /* Enable scroll if needed */\r\n  background-color: rgba(0, 0, 0, 0.4); /* Black with opacity */\r\n}\r\n\r\n/* Modal Content Styling */\r\n.modal-content {\r\n  background-color: #fefefe;\r\n  margin: 10% auto;\r\n  padding: 30px;\r\n  border: 1px solid #888;\r\n  width: 70%; /* Increased width to make the modal wider */\r\n  max-width: 600px; /* Set a max width for larger screens */\r\n  border-radius: 8px;\r\n  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);\r\n}\r\n\r\n/* Close Button Styling */\r\n.close-btn {\r\n  color: #aaa;\r\n  float: right;\r\n  font-size: 28px;\r\n  font-weight: bold;\r\n}\r\n\r\n.close-btn:hover,\r\n.close-btn:focus {\r\n  color: black;\r\n  text-decoration: none;\r\n  cursor: pointer;\r\n}\r\n\r\n/* Modal Title Styling */\r\nh2 {\r\n  font-size: 24px;\r\n  margin-bottom: 20px;\r\n  text-align: center;\r\n}\r\n\r\n/* Add margin to the modal rows for better spacing */\r\n.modal-row {\r\n  margin-bottom: 20px; /* Increased margin for more space between inputs */\r\n}\r\n\r\n/* Label Styling */\r\n.modal-row label {\r\n  display: block;\r\n  font-size: 14px;\r\n  margin-bottom: 8px;\r\n  font-weight: bold;\r\n}\r\n\r\n/* Input Fields Styling */\r\n.modal-row input {\r\n  width: 100%;\r\n  padding: 12px;\r\n  font-size: 16px;\r\n  border: 1px solid #ccc;\r\n  border-radius: 5px;\r\n  margin-bottom: 10px; /* Added margin-bottom for spacing */\r\n}\r\n\r\n.modal-row input:focus {\r\n  outline: none;\r\n  border-color: #4CAF50;\r\n}\r\n\r\n/* Button Styling for Save and Cancel */\r\n.modal-buttons {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  gap: 10px;\r\n}\r\n\r\n.save-btn,\r\n.cancel-btn {\r\n  background-color: transparent;\r\n  color: #fff;\r\n  border: 2px solid;\r\n  padding: 8px 20px;\r\n  font-size: 16px;\r\n  border-radius: 5px;\r\n  cursor: pointer;\r\n  transition: all 0.3s ease;\r\n  width: 48%;\r\n}\r\n\r\n.save-btn {\r\n  border-color: #75c4d6;\r\n  color: #75c4d6;\r\n}\r\n\r\n.save-btn:hover {\r\n  background-color: #75c4d6;\r\n  color: white;\r\n}\r\n\r\n.cancel-btn {\r\n  border-color: #f44336;\r\n  color: #f44336;\r\n}\r\n\r\n.cancel-btn:hover {\r\n  background-color: #f44336;\r\n  color: white;\r\n}\r\n\r\n/* Hover Effect for Buttons */\r\n.save-btn:hover,\r\n.cancel-btn:hover {\r\n  opacity: 0.8;\r\n}\r\n\r\n/* Button Disabled State */\r\nbutton:disabled {\r\n  background-color: #ccc;\r\n  cursor: not-allowed;\r\n}\r\n\r\n/* Styling for the profile row */\r\n.profile-row {\r\n  margin-bottom: 20px;\r\n}\r\n\r\n.profile-row label {\r\n  font-weight: bold;\r\n}\r\n\r\n.profile-row input {\r\n  width: 100%;\r\n  padding: 10px;\r\n  margin-top: 5px;\r\n  font-size: 16px;\r\n  border: 1px solid #ddd;\r\n  border-radius: 5px;\r\n}\r\n\r\n.profile-row input:disabled {\r\n  background-color: #f0f0f0;\r\n}\r\n\r\n/* Profile Container */\r\n.profile-container {\r\n  max-width: 800px;\r\n  margin: 0 auto;\r\n}\r\n\r\n.profile-content {\r\n  display: flex;\r\n  flex-wrap: wrap;\r\n  justify-content: space-between;\r\n}\r\n\r\n.profile-picture {\r\n  width: 30%;\r\n  text-align: center;\r\n}\r\n\r\n.profile-picture img {\r\n  width: 100px;\r\n  height: 100px;\r\n  border-radius: 50%;\r\n}\r\n\r\n.profile-info {\r\n  width: 65%;\r\n}\r\n\r\n/* Change Button */\r\n.change-btn {\r\n  color: #4CAF50;\r\n  cursor: pointer;\r\n  font-weight: bold;\r\n  margin-top: 5px;\r\n  display: inline-block;\r\n}\r\n\r\n.change-btn:hover {\r\n  color: #45a049;\r\n}\r\n\r\n/*statistic styling*/\r\n/* Main Content */\r\n.main-content-statistic {\r\n  max-width: 1400px;\r\n  margin: 0 auto;\r\n  padding: 120px 2rem 2rem;\r\n  display: grid;\r\n  gap: 2rem;        \r\n  padding-top: 1200px; /* Sesuaikan dengan tinggi navbar Anda */\r\n}\r\n\r\n.stats-grid {\r\n  display: grid;\r\n  grid-template-columns: 2fr 1fr;\r\n  gap: 2rem;\r\n}\r\n\r\n.card {\r\n  background: rgba(255, 255, 255, 0.95);\r\n  backdrop-filter: blur(20px);\r\n  border-radius: 20px;\r\n  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.08);\r\n  padding: 2rem;\r\n  border: 1px solid rgba(255, 255, 255, 0.2);\r\n}\r\n\r\n/* Basic Stats Section */\r\n.section-title-stat {\r\n  font-size: 1.75rem;\r\n  font-weight: 700;\r\n  color: #0f172a;\r\n  margin-bottom: 0.5rem;\r\n  position: relative;\r\n}\r\n\r\n.section-title-stat::after {\r\n  content: '';\r\n  position: absolute;\r\n  bottom: -8px;\r\n  left: 0;\r\n  width: 60px;\r\n  height: 4px;\r\n  background: linear-gradient(90deg, #3b82f6, #06b6d4);\r\n  border-radius: 2px;\r\n}\r\n\r\n.section-subtitle-stat {\r\n  font-size: 1rem;\r\n  color: #64748b;\r\n  margin-bottom: 2rem;\r\n  margin-top: 1rem;\r\n}\r\n\r\n.stats-overview {\r\n  display: grid;\r\n  grid-template-columns: repeat(3, 1fr);\r\n  gap: 1rem;\r\n  margin-bottom: 2rem;\r